body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.menu {
  float: left; }

.material-icons.add-button {
  float: right;
  padding-right: 1%; }

.center {
  display: block;
  text-align: center;
  background-color: white;
  position: absolute;
  z-index: -1;
  width: 100%; }

li {
  list-style-type: none; }

.app {
  display: block;
  background-color: #d7ccff; }

button {
  border: 0.5px solid black; }

.list-modify-button {
  display: block;
  float: right;
  margin-right: 1%; }

.popup {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  background-color: rgba(0, 0, 0, 0.5); }

.popup_inner {
  position: absolute;
  left: 25%;
  right: 25%;
  top: 25%;
  bottom: 25%;
  margin: auto;
  background: white; }

.popup_button.left {
  float: left;
  margin-left: 10%; }

.popup_button.right {
  float: right;
  margin-right: 10%; }

.popup_button {
  width: 32%;
  background-color: whitesmoke;
  border-radius: 10px;
  border: 1px solid rebeccapurple; }

.title {
  color: rebeccapurple; }

